import React from 'react';
import Button from '@material-ui/core/Button';
import Icon from '@material-ui/icons/Cancel';
import IconButton from '@material-ui/core/IconButton';

export default function Video(props) {
  return (
    <div className="scanner-container-top">
      <div id="video" className={"scanner-container " + (props.show ? 'show' : '')} >
        <IconButton color="secondary" className="cancel-scan-btn" onClick={props.onScanCancel}>
          <Icon />
        </IconButton>
      </div>
    </div>

  )
}



