import React from 'react';
import {ICONS} from './icons.js'
import Button from '@material-ui/core/Button';
import './css/studentInfo.css'

export default function StudentInfo(props) {
  const {name, degree, year, token, activeStudent, formOfStudy, specialty} = props.data
  const student = props.data

  return (
    <div className="student">

      <div className="data">
        <div className="student--name">{name}</div>
        <div className="student--info">
          <div className="student--specialty">{specialty}</div>
          <div className="student--year">{degree} – {year} курс</div>
          <div className="student--form">{formOfStudy}</div>
        </div>
      </div>

      <div className="actions">
        {!activeStudent &&
          <Button variant="flat" color="primary" onClick={() => props.onSubmit(student)}>обрати</Button>
        }
      </div>

    </div>
  )
}



