import React from 'react'

import axios from 'axios'
import Raven from 'react-raven'
import { MuiThemeProvider, createMuiTheme } from '@material-ui/core/styles';
import Quagga from 'quagga'
import CheckIn from './CheckIn.js'
import blue from '@material-ui/core/colors/blue';
import red from '@material-ui/core/colors/red';
import Button from '@material-ui/core/Button';
import {ICONS} from './icons.js'
import logo from './img/logo.jpg'
import votingImg from './img/voting.png'

const theme = createMuiTheme({
  palette: {
    primary: {
      light: blue[300],
      main: blue[500],
      dark: blue[700],
      contrastText: '#fff',
    },
    secondary1: {
      light: '#ff7961',
      main: '#f44336',
      dark: '#ba000d',
      contrastText: '#000',
    },
    secondary: {
      light: red[300],
      main: red[500],
      dark: red[700],
      contrastText: '#fff'
    }
  },
});

/******

sessionStatus

100 – session just opened, choosing docType

200 – success and data for ticketId
201 –
202 – error for ticketId [already voted]
203 – error for ticketId [already in session]
204 – error for ticketId [not found]

300 – sent docType!=ticket, fullName
301 – success and data for docType!=ticket and fullName
302 - error for docType!=ticket and fullName [already voted]
303 - error for docType!=ticket and fullName [already in session]
304 - error for docType!=ticket and fullName [not found]

400 – submitted


******/

export default class extends React.Component {
  state = {
    loggedIn: false,
    sessionIsOpen: false,
    sessionStatus: null,
    status: {
      type: 'info',
      message: ''
      // showIcon: false,
      // icon: ICONS.readyIcon,
    },
    foundStudents: []

  }

  componentDidMount() {
    axios.defaults.baseURL = 'https://elists-dev.herokuapp.com/api/elists'
    axios.defaults.withCredentials = true
    this.getAuth()
    this.closeSessions()

  }

  getAuth() {
    axios.post('/me', {})
      .then(res => {
        this.setState({
          loggedIn: true
        })
      })
      .catch(err => {
        this.setState({
          loggedIn: false
        })
        window.location.href = "https://elists-dev.herokuapp.com/admin/login?next=/elists/front"

      })
  }


  closeSessions() {
    axios.post('close_sessions', {})
      .catch(err => {
        this.handleError(err)
      })
  }

  openSession () {
    let data = {}

    axios.post('/start_new_session', data)
      .then(res => {
        console.log(res)
        this.setState({
          sessionIsOpen: true,
          sessionStatus: 100,
          checkInSessionToken: res.data.data.check_in_session.token,
          status: {
            type: 'info',
            message: 'Оберіть тип документа'
          }
        })
      })
      .catch(err => {
        this.handleError(err)
      })
  }

  searchStudentByTicketNumber (ticketNum) {
    let data = {}
    data.check_in_session_token = this.state.checkInSessionToken
    data.student = {}
    data.student.ticket_number = ticketNum

    axios.post('/search_by_ticket_number', data)
    .then(res => {
        console.log(res)
        const studentData = res.data.data.student.data
        const studentObj = res.data.data.student

        let student = {
          ticketNum: ticketNum,
          name: studentData.full_name,
          degree: studentData.educational_degree === 1 ? 'Бакалавр' : 'Магістр',
          formOfStudy: studentData.form_of_study === 1 ? 'Денна' : 'Заочна',
          specialty: studentData.specialty,
          year: studentData.year,
          token: studentObj.token
        }

        let foundStudents = this.state.foundStudents.slice()
        foundStudents[0] = {...foundStudents[0], ...student}

        // show student info
        this.setState({
          docType: 0,
          docNumber: ticketNum,
          foundStudents: foundStudents,
          sessionStatus: 200,
          status: {
            type: 'info',
            message: 'Підтвердіть вибір студента'
          }
        })
    })
    .catch(err => {
      this.handleError(err, 302)
    })

  }

  submitStudent(student) {
    let data = {}
    data.student = {}
    data.student.token = student.token
    data.student.doc_type = this.state.docType
    data.student.doc_num = this.state.docNumber
    data.check_in_session_token = this.state.checkInSessionToken

    axios.post('/submit_student', data)
      .then(res => {
        console.log(res)
        this.setState({
          activeStudent: student,
          status: {
            type: 'info',
            message: 'Видайте бюлетень'
          }
        })
      })
      .catch(err => {
        console.error(err.response.data.error)
      })
  }

  handleError(err, status) {
    let errData = err
    if (err.response) errData= err.response.data.error
    console.error(errData)
    this.setState({
      error: errData,
      sessionStatus: status
    })
  }

  cancelSession () {
    let data = {}
    data.check_in_session_token = this.state.checkInSessionToken

    axios.post('/cancel_session', data)
      .then(res => {
        this.onSessionEnd()
        })
      .catch(err => {
        this.handleError(err)
      })

  }

  completeSession () {
    let data = {}
    data.check_in_session_token = this.state.checkInSessionToken

    axios.post('/complete_session', data)
      .then(res => {
        this.onSessionEnd()
      })
      .catch(err => {
        this.handleError(err)
      })
  }

  onSessionEnd () {
    this.setState({
      activeStudent: null,
      sessionIsOpen: false,
      sessionStatus: null,
      checkInSessionToken: null,
      foundStudents: []
    })
    Quagga.stop()
  }

  render() {

    return (
      <div className="page-content-wrapper ">

        <Raven dsn='https://6114eea799a146298b71db08a24d036d@sentry.io/1241630' />

        <MuiThemeProvider theme={theme}>
            <div className="header-and-content">

              <header>
                <div className="title">ЦВК</div>
                <div className="logo">
                  <img src={logo} alt="logo" height="45" />
                </div>
                <div className="title">КНУ</div>
              </header>

              <div className="content ">

                {
                  this.state.loggedIn && !this.state.sessionIsOpen &&
                  <div className="card new-session-card">
                    <img src={votingImg} alt="" />
                    <div className="card-footer">
                      <Button variant="contained" color="primary" onClick={this.openSession.bind(this)}>почати</Button>
                    </div>
                  </div>
                }

                {
                  this.state.loggedIn && this.state.sessionIsOpen &&
                  <CheckIn
                    activeStudent={this.state.activeStudent}
                    status={this.state.status}
                    foundStudents={this.state.foundStudents}
                    onStudentSubmit={this.submitStudent.bind(this)}
                    onScanStart={this.initScan.bind(this)}
                    onScanCancel={this.cancelScan.bind(this)}
                    onCancelSession={this.cancelSession.bind(this)}
                    onCompleteSession={this.completeSession.bind(this)}
                    sessionStatus={this.state.sessionStatus}
                  />
                }

              </div>

            </div>

            <footer>
              <p className="small no-margin pull-left sm-pull-reset">
                <span className="hint-text">Copyright &copy; 2018 </span>
                <span className="font-montserrat">KNU</span>.
                <span className="hint-text">All rights reserved. </span>
              </p>
              <p>
                <span className="hint-text">Hand-crafted &amp; made with Love</span>
              </p>
            </footer>
        </MuiThemeProvider>
      </div>

    )
  }

  initScan() {
    this.setState({isLoading: true})
    Quagga.init({
        numOfWorkers: getCoresNumber(),
        inputStream : {name : "Live",type : "LiveStream",target: document.querySelector('.scanner-container')},
        decoder : {readers : ["code_128_reader"]},
        frequency: 10
      }, (err) => {
          if (err) {
            console.log(err)
            return
          }

          Quagga.start()

          this.setState({
            status: {
              type: 'info',
              message: 'Піднесіть студентський квиток до камери'
            }
          })

          this.onDetected()
      });


  }

  cancelScan() {
    Quagga.stop();
    this.setState({
      sessionsStatus: 100,
      status: {
        type: 'info',
        message: 'Оберіть тип документа'
      }
    })
  }

  onDetected() {
    Quagga.onDetected((data) => {
        const result = data.codeResult.code
        if (result.length === 8) {
          // playSuccessSound()
          Quagga.stop()
          this.searchStudentByTicketNumber(result)
        } else {
          alert('Card has to contain 8 digits')
        }
    })
  }

}


function getCoresNumber() {
  try {
    let cores = navigator.hardwareConcurrency
    return cores
  } catch (err) {
    console.error(err)
  }
}



